import { initInputMask } from "./inputmask.js"
import JustValidate from "just-validate"
import JustValidatePluginDate from "just-validate-plugin-date"
import Swal from "sweetalert2"

initInputMask()

const defaultErrorColor = "#ff831e"
const defaultRequiredMessage = "Ой, вы забыли заполнить это поле"
const alwaysTrueValidator = { validator: () => true }
const defaultOnSuccess = () => {
    Swal.fire("Спасибо!", "Мы скоро свяжемся с вами", "success")
}
const requiredRule = { rule: "required", errorMessage: "Это поле обязательно" }

function getPhoneRules(parentSelector) {
    const noDot = stripDot(parentSelector)
    return [
        { rule: "required", errorMessage: "Ой, без телефона никуда!" },
        {
            validator: function (value, context) {
                const inputmask = context[`${parentSelector} #${noDot}-phone`].elem.inputmask
                if (!inputmask) return false
                const phone =
                    context[`${parentSelector} #${noDot}-phone`].elem.inputmask.unmaskedvalue()
                return !!Number(phone)
            },
            errorMessage: "Телефонный номер может содержать только цифры!",
        },
        {
            validator: function (value, context) {
                const inputmask = context[`${parentSelector} #${noDot}-phone`].elem.inputmask
                if (!inputmask) return false
                const phone = inputmask.unmaskedvalue()
                return phone.length > 10
            },
            errorMessage: "Телефонный номер слишком короткий, в нем меньше 11 цифр",
        },
        {
            validator: function (value, context) {
                const inputmask = context[`${parentSelector} #${noDot}-phone`].elem.inputmask
                if (!inputmask) return false
                const phone =
                    context[`${parentSelector} #${noDot}-phone`].elem.inputmask.unmaskedvalue()
                return phone.length < 12
            },
            errorMessage: "Телефонный номер длинноват, в нем больше 11 цифр",
        },
    ]
}
const emailRules = [
    requiredRule,
    {
        rule: "email",
        errorMessage: "Ой, это не похоже на почту",
    },
]
const agreementRules = [
    {
        rule: "required",
        errorMessage: "Согласитесь с соглашением, пожалуйста",
    },
]
function validateCallback() {
    const parentSelector = ".callback-popup"
    const selectors = [
        {
            name: "name",
            rules: [requiredRule, alwaysTrueValidator],
        },
        {
            name: "phone",
            rules: getPhoneRules(parentSelector),
        },
        {
            name: "email",
            rules: emailRules,
        },
        {
            name: "time",
            rules: [requiredRule, alwaysTrueValidator],
        },
        {
            name: "message",
            rules: [requiredRule, alwaysTrueValidator],
        },
        {
            name: "agreement",
            wrapper: null,
            rules: agreementRules,
        },
    ]
    const validate = new JustValidate(parentSelector, {
        validateBeforeSubmitting: true,
        errorLabelStyle: {
            color: defaultErrorColor,
        },
    })
    initFields(validate, selectors, parentSelector)
        .onValidate((options) => onValidate(options, selectors, parentSelector))
        .onSuccess(defaultOnSuccess)
}
function validateContact() {
    const parentSelector = ".contacts-popup"

    const selectors = [
        { name: "name", rules: [requiredRule, alwaysTrueValidator] },
        { name: "phone", rules: getPhoneRules(parentSelector) },
        {
            name: "email",
            rules: emailRules,
        },

        { name: "message", rules: [requiredRule, alwaysTrueValidator] },
        {
            name: "agreement",
            wrapper: null,
            rules: agreementRules,
        },
    ]
    const validate = new JustValidate(parentSelector, {
        validateBeforeSubmitting: true,
        errorLabelStyle: {
            color: defaultErrorColor,
        },
    })

    initFields(validate, selectors, parentSelector)
        .onValidate((options) => onValidate(options, selectors, parentSelector))
        .onSuccess(defaultOnSuccess)
}
function initFields(validate, selectors, parentSelector) {
    const noDot = stripDot(parentSelector)
    for (const selector of selectors) {
        for (const selector of selectors) {
            const fieldSelector = `${parentSelector} #${noDot}-${selector.name}`
            // console.log("fieldSelector", fieldSelector)
            validate.addField(fieldSelector, selector.rules, {
                errorsContainer: `.${selector.name}-wrapper .just-validate-error-container`,
            })
        }
    }
    return validate
}
function validatePlanTrip() {
    const parentSelector = ".plan-trip"
    const dateFormat = "dd.MM.yyyy"
    const selectors = [
        { name: "phone", rules: getPhoneRules(parentSelector) },
        { name: "trip-goal", rules: [{ rule: "required", errorMessage: defaultRequiredMessage }] },
        {
            name: "start-date",
            rules: [
                { rule: "required", errorMessage: defaultRequiredMessage },
                {
                    plugin: JustValidatePluginDate((fields) => {
                        const selector = `${parentSelector} #plan-trip-end-date`
                        if (fields[selector]) {
                            if (fields[selector].elem.value == "") return { format: dateFormat }
                            return {
                                format: dateFormat,
                                isBeforeOrEqual: fields[selector].elem.value,
                            }
                        } else return {}
                    }),
                    errorMessage: "Дата должна быть равна или после конечной ",
                },
            ],
        },
        {
            name: "end-date",
            rules: [
                {
                    plugin: JustValidatePluginDate((fields) => {
                        const selector = `${parentSelector} #plan-trip-start-date`
                        if (fields[selector]) {
                            if (fields[selector].elem.value == "") return { format: dateFormat }
                            return {
                                format: dateFormat,
                                isAfterOrEqual: fields[selector].elem.value,
                            }
                        } else return {}
                    }),
                    errorMessage: "Дата должна быть равна или до стартовой",
                },
            ],
        },
        {
            name: "adult-count",
            rules: [
                //  [{ rule: "required", errorMessage: defaultRequiredMessage }],
                {
                    rule: "minNumber",
                    value: 1,
                    errorMessage: "Групп меньше одного человека не бывает",
                },
                {
                    rule: "maxNumber",
                    value: 100,
                    errorMessage: "Группу больше 100 человек мы не потянем",
                },
            ],
        },
        {
            name: "child-count",
            rules: [
                //  [{ rule: "required", errorMessage: defaultRequiredMessage }],
                {
                    rule: "minNumber",
                    value: 1,
                    errorMessage: "Групп меньше одного человека не бывает",
                },
                {
                    rule: "maxNumber",
                    value: 100,
                    errorMessage: "Группу больше 100 человек мы не потянем",
                },
            ],
        },
        {
            name: "vacation-type",
            rules: [alwaysTrueValidator],
        },
        {
            name: "residence-type",
            rules: [alwaysTrueValidator],
        },
        {
            name: "service-level",
            rules: [alwaysTrueValidator],
        },
        {
            name: "transfer-type",
            rules: [alwaysTrueValidator],
        },
    ]
    const validate = new JustValidate(parentSelector, {
        validateBeforeSubmitting: true,
        errorLabelStyle: {
            color: defaultErrorColor,
        },
    })
    initFields(validate, selectors, parentSelector)
        .onValidate((options) => onValidate(options, selectors, parentSelector))
        .onSuccess(defaultOnSuccess)

    for (const selector of selectors) {
        const noDot = stripDot(parentSelector)
        const fieldSelector = `${parentSelector} #${noDot}-${selector.name}`
        document.querySelector(fieldSelector).addEventListener("change", (e) => {
            // console.log("change", e.target, e.target.value)
            validate.revalidateField(fieldSelector).then((isValid) => {
                revalidateField(validate, parentSelector, selector)
            })
        })
    }
}
export function forms() {
    validateCallback()
    validateContact()
    validatePlanTrip()

    const fromPopups = document.querySelectorAll(".form-popup")
    for (const formPopup of fromPopups) {
        const agreement = formPopup.querySelector("#callback-popup-agreement")
        if (!agreement) return
        const submitButton = formPopup.querySelector("button[type=submit]")
        agreement.addEventListener("change", (e) => {
            submitButton.disabled = !agreement.checked
        })
        document.addEventListener("DOMContentLoaded", (e) => {
            submitButton.disabled = !agreement.checked
        })
    }
}
function onValidate(options, selectors, parentSelector) {
    // console.log("---------------")
    //  console.log("onvalidate", options)
    //  console.log("onvalidate", selectors)
    const noDot = stripDot(parentSelector)
    for (const field of Object.keys(options.fields)) {
        if (options.isSubmitted || (!options.isSubmitted && options.fields[field].touched)) {
            const selector = selectors.find((selector) => {
                const needle = `${parentSelector} #${noDot}-${selector.name}`
                return field === needle
            })
            // console.log("selector", selector, "field", field)
            markField(selector, parentSelector, options.fields[field].isValid, !options.isSubmitted)
        }
    }
}

function markField(selector, parentSelector, isValid, ignoreEmpty = false) {
    //  console.log("selector", selector)
    const noDot = stripDot(parentSelector)
    const wrapperClassName = `${parentSelector}-${selector.name}-field`
    //  console.log("wrapperClassName", wrapperClassName)
    const wrapper = document.querySelector(wrapperClassName)
    if (!wrapper) return
    const removeClass = removeClassName.bind(null, wrapper)
    const addClass = addClassName.bind(null, wrapper)
    if (isValid) {
        //   console.log(
        //       "value",
        //       `'${document.querySelector(`${parentSelector} #${noDot}-${selector.name}`).value}'`,
        //       ignoreEmpty
        //   )
        if (document.querySelector(`${parentSelector} #${noDot}-${selector.name}`).value !== "") {
            removeClass("just-validate-error-field-wrapper")
            addClass("just-validate-success-field-wrapper")
        } else {
            if (ignoreEmpty) {
                removeClass("just-validate-error-field-wrapper")
                removeClass("just-validate-success-field-wrapper")
            } else {
                removeClass("just-validate-error-field-wrapper")
                addClass("just-validate-success-field-wrapper")
            }
        }
    } else {
        removeClass("just-validate-success-field-wrapper")
        addClass("just-validate-error-field-wrapper")
    }
}
function revalidateField(validator, parentSelector, selector) {
    const noDot = stripDot(parentSelector)
    validator
        .revalidateField(`${parentSelector} #${noDot}-${selector.name}`)
        .then(function (isValid) {
            // console.log(
            //     selector.field,
            //     selector.wrapper,
            //     document.querySelector(selector.field).value,
            //     isValid
            // )
            markField(selector, parentSelector, isValid, true)
        })
}
import { debounce } from "./functions.js"
export function noOutlineOnClick(...selectors) {
    const mouseDownClassname = "using-mouse"
    function foo(e) {
        for (const selector of selectors) {
            const closest = e.target.closest(selector)
            if (closest) {
                closest.classList.add(mouseDownClassname)
                break
            }
        }
    }
    document.addEventListener("mousedown", foo)
    // noUiSlider apparently stops mousedown propagation
    document.addEventListener("mousemove", debounce(foo, 200))
    document.addEventListener("keydown", (e) => {
        for (const selector of selectors) {
            const closest = e.target.closest(selector)
            if (closest) {
                if (e.code === "Tab") {
                    closest.classList.remove(mouseDownClassname)
                }
                break
            }
        }
    })
}
import { addClassName, removeClassName } from "./functions.js"

function stripDot(className) {
    return className.substring(1)
}

forms()
noOutlineOnClick(
    ".filter__list-item",
    ".burger-list-item",
    ".burger__main-list-item",
    ".burger__secondary-list-item",
    ".button",
    ".close-button",
    "form"
)
