import Inputmask from "inputmask"

let isComplete
export function initInputMask() {
    Inputmask.default({
        mask: "+9 (999) 999-99-99",
        clearMaskOnLostFocus: false,
        oncomplete: function () {
            //   console.log("complete")
            //   button.removeAttribute("disabled")
            isComplete = true
        },
        //  onKeyDown: function () {
        //      console.log(phone.inputmask.unmaskedvalue(), phone.inputmask.isComplete())
        //      if (!phone.inputmask.isComplete()) {
        //          // if (!button.hasAttribute("disabled")) button.setAttribute("disabled", true)
        //          error.textContent = ""
        //          error.classList.remove("message--visible")
        //      }
        //      isComplete = phone.inputmask.isComplete()
        //  },
        onKeyValidation: function () {
            foo.apply(this)
        },
        onBeforePaste: function () {
            foo.apply(this)
        },
        onBeforeMask: function () {
            foo.apply(this)
        },
        oncleared: function () {
            foo.apply(this)
        },
    }).mask(".phone")
    function foo() {
        if (!this.inputmask) return
        if (this.inputmask.unmaskedvalue().length === 0) {
            if (!this.classList.contains("empty")) this.classList.add("empty")
        } else {
            this.classList.remove("empty")
        }
        isComplete = this.inputmask.isComplete()
    }
}
